.desc-item{
    background: rgb(89,6,80);
    background: linear-gradient(312deg, rgba(89,6,80,1) 4%, rgba(160,40,167,1) 23%, rgba(173,25,169,1) 34%, rgba(189,44,184,1) 78%, rgba(98,9,94,1) 95%); 
}

.desc-title1{
    -webkit-text-stroke: 2px white;
    
}
.desc-title2{
    -webkit-text-stroke: 2px black;
    
}

.about-btn-1:before{
    content: '';
    position: absolute;
    width: 0%;
    height: 100%;
    background:url("../../assets/images/btn-bg2.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 100%;
    top: 0;
    left: 0;
 
    z-index: -1;
  

}
.about-btn-1:hover:before{
    width: 100%;
    bottom: 0%;


  
}
.about-btn-1:hover{
    border: transparent;
}

.about-btn-2:before{
    content: '';
    position: absolute;
    width: 0%;
    height: 100%;
    background:url("../../assets/images/btn-bg1.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 100%;
    top: 0;
    left: 0;
    z-index:-1;
  

}
.about-btn-2:hover:before{
    width: 100%;
    bottom: 0%;


  
}
.about-btn-2:hover{
    border-color: transparent;
    background-color: transparent;
}